import React, { useState, useEffect } from 'react';
import BlogPost from './BlogPost';
import { Container, Grid } from '@mui/material';
import Divider from '@mui/material/Divider';

function App() {
  const [markdownContent, setMarkdownContent] = useState('');

  useEffect(() => {
    const fetchMarkdownContent = async () => {
      try {
        const response = await fetch('/blogs/Тиждень 1 3f204499fc2e4297a016d38a665afc8e.md');
        const content = await response.text();
        setMarkdownContent(content);
      } catch (error) {
        console.error('Error fetching Markdown content:', error);
      }
    };

    fetchMarkdownContent();
  }, []);

  return (
  
  <Container >
  <Grid container>
    <Grid md={6}>
    <h1>Roman Academy</h1>
    </Grid>
    <Grid md={6}>
    </Grid>
  </Grid>
<Grid container>
  <Grid md={8} pr={5} >
    <BlogPost mycontent={markdownContent} />
  </Grid>
  <Divider orientation="vertical" flexItem />
  <Grid md={3} pl={5} >
    <h2>Інформація:</h2>
   
    <p>Цей курс є перекладом. Також є доповнення, які допомогли мені особисто.</p>
     <p>Я не перекладав деякі поняття, тому що так чи інакше вони будуть зустрічатися в інтеренеті і важливо розуміти, що це означає.</p>
  </Grid>
    
  </Grid>

  </Container>
  );
}

export default App;
